import React, { Component } from 'react';
import { TopBar, BudgetButton, Footer } from '../Fixed';

import style from './notfound.module.scss';

export default class NotFoundLayout extends Component {

    render() {

        return (
            <div className={[style.fullsize].join(' ')}>
                <TopBar />
                <h1>404 - NÃO ENCONTRADO</h1>
                <BudgetButton />
                <div className={[style.white].join(' ')}>
                
                    <div id={[style.animation].join(' ')}>
                    </div>

                    <div className={[style.animation_text].join(' ')}>
                        <h2>Não encontrámos o que procura, mas já temos um dos nossos colaboradores a tratar disso.</h2>
                        <h3>Entretanto, pode voltar à nossa página <a href='/'>inicial</a>.</h3>
                    </div>
                </div>
                <Footer />
            </div>
        );
    }
}