import React, { Component } from 'react';
import { TopBar, Footer } from '../Fixed';

import style from './recruitment.module.scss';
import RecruitmentForm from '../Forms/recruitment';

interface RecruitmentLayoutProps {

}

interface RecruitmentLayoutState {

}

export default class RecruitmentLayout extends Component<RecruitmentLayoutProps, RecruitmentLayoutState> {

    render() {

        return (
            <div className={[style.fullsize].join(' ')}>
                <TopBar />
                <h1>Recrutamento</h1>
                <RecruitmentForm />
                <Footer />
            </div>
        );
    }
}