import React, { Component } from 'react';
import { Collapse, Navbar, NavbarToggler, NavbarBrand, Nav, NavItem, NavLink } from 'reactstrap';
import { NavlinksHttpEntity } from '../../../entities/Entities';


import style from './style.module.scss';
import Logo from '../../../resources/site_logo.png';
import navlinks from './navlinks';

//TODO: make the topbar more responsive

interface TopBarProps {
    light?: boolean;
    dark?: boolean;
    fixed?: string;
    color?: string;
    role?: string;
    expand?: boolean | string;
    tag?: string | Function;
}

interface TopBarState {
    collapsed?: any;
    isOpen: any;
}

export default class TopBar extends Component<TopBarProps, TopBarState> {

    constructor(props: any) {
        super(props);

        this.toggle = this.toggle.bind(this);
        this.state = {
            isOpen: false
        };
    }

    toggle() {

        this.setState({
            isOpen: !this.state.isOpen
        });
    }

    renderNavlinks(navlinks: NavlinksHttpEntity[]): JSX.Element {

        return (
            <Nav className={[style.navbar].join(' ')} navbar>

                {navlinks.map((navlink: NavlinksHttpEntity, index: number) => {

                    return (
                        <NavItem key={index}>
                            <NavLink key={index} href={navlink.url}>{navlink.showingText.toUpperCase()}</NavLink>
                        </NavItem>
                    );
                })}
            </Nav>
        );
    }

    render() {

        const {
            isOpen
        } = this.state;

        return (
            <div className={'topbar'}>
                <Navbar fixed="top" className={[style.topbar].join(' ')} expand="md">
                    <NavbarBrand href="/"><img className={[style.logo].join(' ')} src={Logo} alt={'Mega Loja Borja Reis'} /></NavbarBrand>

                    <NavbarToggler onClick={this.toggle} />
                    <Collapse isOpen={isOpen} navbar>

                        {this.renderNavlinks(navlinks)}

                    </Collapse>
                </Navbar>
            </div>
        );
    }
}

