import React, { Component } from 'react';
import { Button, Form, FormGroup, Label, Input, FormText, Container, Col, CustomInput } from 'reactstrap';

import style from './style.module.scss';

export default class RecruitmentForm extends Component {

    render() {
        return (
            <Container fluid={true} className={[style.form].join(' ')}>
                <Col sm="12" md={{ size: 8, offset: 2 }}>
                    <Form>
                        <FormGroup>
                            <Label for="name" size='lg'>Nome</Label>
                            <Input name="name" id="formName" placeholder="Nome completo" />
                        </FormGroup>
                        <FormGroup>
                            <Label for="email" size='lg'>E-mail</Label>
                            <Input type="email" name="email" id="formEmail" placeholder="email@dominio.pt" />
                        </FormGroup>
                        <FormGroup>
                            <Label for="job">Tipo de cargo</Label>
                            <Input type="select" name="selectJob" id="formSelectJob">
                                <option>1 - Carga</option>
                                <option>2 - Vendas</option>
                                <option>3 - Designer de Interiores</option>
                                <option>4 - Gestor de Loja</option>
                                <option>5 - Outros</option>
                            </Input>
                        </FormGroup>
                        <FormGroup>
                            <Label for="exampleSelectMulti">Lojas</Label>
                            <div>
                                <CustomInput type="switch" id="exampleCustomSwitch" name="customSwitch" label="Materiais de Construção" />
                                <CustomInput type="switch" id="exampleCustomSwitch2" name="customSwitch" label="Mobiliário e Decoração" />
                            </div>
                        </FormGroup>
                        <FormGroup>
                            <Label for="exampleText">Breve introdução</Label>
                            <Input type="textarea" name="text" id="exampleText" />
                        </FormGroup>
                        <FormGroup>
                            <Label for="exampleFile">Carregar CV</Label>
                            <Input type="file" name="file" id="exampleFile" />
                            <FormText className={[style.text_red].join(' ')}>
                                Apenas são aceites ficheiros .doc, .docx ou .pdf
                            </FormText>
                        </FormGroup>
                        <FormGroup check>
                            <Label check>
                                <Input type="checkbox" />{' '}
                                Concordo partilhar estas informações com a empresa
                            </Label>
                        </FormGroup>
                        <Button>Submeter</Button>
                    </Form>
                </Col>
            </Container>
        );
    }
}
