const About: string = 'Fundada a Fevereiro de 1996, em Angra do Heroísmo, a Mega Loja Borja Reis nasceu da capacidade empreendedora de Francisco de Borja Vasconcelos Oliveira Reis, que alimentou o sonho de criar a sua própria empresa. O seu carácter determinado e a vontade de trabalhar foram chave de sucesso para vencer no negócio que foi crescendo gradualmente ano após ano.\n' +
'\nEm 1997, o crescimento sustentado conduziu á mudança de instalações para Avenida Álvaro Martins Homem, havendo a necessidade de expandir e diversificar os produtos, para além da actividade de comércio de Materiais de Construção para Acabamentos dá-se inicio à comercialização de Cozinhas, Decoração, Mobiliário e Electrodomésticos.\n' +
'\nA 1 de Dezembro de 2002, a Mega Loja Borja Reis, inaugura um espaço, na Zona Industrial de Angra do Heroísmo com cerca de 800m2, espaço este destinado ao comércio de Mobiliário, Decoração e Electrodomésticos. Mais tarde, e com o alargamento das instalações, a Secção de Materiais de Construção para Acabamentos e Cozinhas é transferida para o Zona Industrial.\n' +
'\nA Mega Loja Borja Reis, cultiva uma grande experiência e variedade na escolha dos seus produtos, que aliada ao profissionalismo e atendimento personalizado dos seus cerca de 40 colaboradores, permite oferecer uma moderna gama de produtos, cujos parâmetros de qualidade, são uma garantia para os seus consumidores.\n' +
'\nO percurso da Mega Loja Borja Reis foi sempre marcado por um desejo de evolução e pela vontade de crescer. Foi-se assim construindo, passo a passo, a imagem de uma empresa de referência no mercado. Hoje e sempre a Mega Loja Borja Reis aposta no futuro. Futuro este que enfrenta com confiança, continuando a apostar na qualidade e inovação, permitindo com o seu vasto leque de produtos, que todos os clientes encontrem a melhor solução para criação, renovação e remodelação dos seus espaços.\n'

export default About;