import 'bootstrap/dist/css/bootstrap.css';
import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { FlyersLayout, IndexLayout, NotFoundLayout, PublicationsLayout, RecruitmentLayout } from './components/Layout/routes';

import './scss/index.scss';
import { library } from '@fortawesome/fontawesome-svg-core';
import { faStroopwafel, faPhone, faMapMarkerAlt, faMobileAlt, faEnvelope, faClipboardList, faRssSquare, faCheckSquare, faArrowCircleUp } from '@fortawesome/free-solid-svg-icons';

library.add(faStroopwafel, faPhone, faMapMarkerAlt, faMobileAlt, faEnvelope, faClipboardList, faRssSquare, faCheckSquare, faArrowCircleUp);

const rootElement: HTMLElement | null = document.getElementById('root');

const routing = (
    <Router>
        <Switch>
            <Route exact path="/" component={IndexLayout} />
            <Route exact path="/publications" component={PublicationsLayout} />
            <Route exact path="/flyers" component={FlyersLayout} />
            <Route exact path="/recruitment" component={RecruitmentLayout} />
            <Route component={NotFoundLayout} />
        </Switch>
    </Router>
);

class Main extends React.Component<{}, {}> {

    render() {

        return routing;
    }
}

ReactDOM.render(<Main />, rootElement);