import React, { Component } from 'react';
import { Col, Row, Container } from 'reactstrap';
import { ImageBrandHttpEntity } from '../../../entities/Entities';

import images from './images';
import style from './style.module.scss';

interface BrandSectionProps {

}

interface BrandSectionState {

}

export default class BrandSection extends Component<BrandSectionProps, BrandSectionState> {

    renderBrands(brandArray: ImageBrandHttpEntity[]): JSX.Element {

        return (
            <Row>
                {brandArray.map((brand: ImageBrandHttpEntity, index: number) => {

                    return (
                        <a href={brand.url}>
                            <img src={brand.image} alt={brand.text.toUpperCase()} key={index}></img>
                        </a>
                    );
                })}
            </Row>
        );
    }

    render() {

        return (
            <Container fluid={true} className={[style.brands].join(' ')} id={'brands'}>
                <Col sm="12" md={{ size: 8, offset: 2 }}>

                    <h1>MARCAS</h1>
                    {this.renderBrands(images)}

                </Col>
            </Container>
        );
    }
}
