import AboutText from './about';
import React, { Component } from 'react';
import { Col, Container } from 'reactstrap';
import AboutUsImg from '../../../resources/about_us.jpg';

import style from './style.module.scss';

export default class AboutSection extends Component<{}, {}> {

    render() {

        return (
            <Container fluid={true} className={[style.section].join(' ')}>
                <img className={[style.about_us].join(' ')} src={AboutUsImg} alt={'Mega Loja Borja Reis'} /> {/* TODO: CHANGE IMAGE */}
                <Col sm="12" md={{ size: 8, offset: 2 }}>
                    <h2>Sobre Nós</h2>
                    <p className={[style.about].join(' ')}>
                        {AboutText}
                    </p>
                </Col>
            </Container>
        );
    }
}