import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import style from './style.module.scss'

interface ScrollToTopButtonProps {
    scrollStepInPx?: any;
    delaynInMs?: number;
}

interface ScrollToTopButtonState {
    intervalId: number;
    isHidden: boolean;
}

export default class ScrollToTopButton extends Component<ScrollToTopButtonProps, ScrollToTopButtonState> {

    constructor(props: ScrollToTopButtonProps) {
        super(props);

        this.state = {
            intervalId: 0,
            isHidden: false
        };

        this.hideButton = this.hideButton.bind(this);
    }

    prev: number = 2000;

    hideButton() {

        const {
            isHidden
        }: ScrollToTopButtonState = this.state;

        (window.scrollY >= this.prev) ?
            !isHidden && this.setState({
                isHidden: true
            }) :
            isHidden && this.setState({
                isHidden: false
            });

        this.prev = window.scrollY;
    }

    componentDidMount() {
        window.addEventListener('scroll', this.hideButton);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.hideButton);
    }

    scrollStep(): void {

        if (window.pageYOffset === 0) {
            clearInterval(this.state.intervalId);
        }
        window.scroll(0, window.pageYOffset - this.props.scrollStepInPx);
    }

    scrollToTop(): void {

        let intervalId = setInterval(
            this.scrollStep.bind(this),
            this.props.delaynInMs
        );

        this.setState({
            intervalId: intervalId
        });
    }

    render() {

        return (
            <button id={[style.button].join(' ')}
                onClick={() => { this.scrollToTop(); }}>
                <FontAwesomeIcon size={'4x'} icon={'arrow-circle-up'} />
            </button>
        );
    }
}