import storesInfo from './stores';
import React, { Component } from 'react';
import { Col, Row, Container } from 'reactstrap';
import { StoreContactInfoHttpEntity } from '../../../entities/Entities';

import style from './style.module.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import googleMaps from '../../../resources/google_maps.png';

export default class ContactSection extends Component<{}, {}> {

    renderStoresInfo(info: StoreContactInfoHttpEntity[]) {

        return (
            <Row>
                {info.map((store: StoreContactInfoHttpEntity, index: number) => {

                    return (
                        <Col key={index}>
                            <h3>{store.name}</h3>

                            <p>
                                <FontAwesomeIcon size={'2x'} icon={'map-marker-alt'} />
                                <span>{store.address}</span>
                            </p>

                            <p>
                                <FontAwesomeIcon size={'2x'} icon={'phone'} />
                                <a href={'tel:+351' + store.phone}>
                                    <span>{store.phone}</span>
                                </a>
                            </p>

                            <p>
                                <FontAwesomeIcon size={'2x'} icon={'mobile-alt'} />
                                <a href={'tel:+351' + store.cellphone}>
                                    <span>{store.cellphone}</span>
                                </a>
                            </p>

                            <p>
                                <FontAwesomeIcon size={'2x'} icon={'envelope'} />
                                <a href={'mailto:' + store.email}>
                                    <span>{store.email}</span>
                                </a>
                            </p>

                            <p>
                                <FontAwesomeIcon size={'2x'} icon={'clipboard-list'} />
                                <span>{store.nif}</span>
                            </p>
                        </Col>
                    );
                })}
            </Row>
        );
    }

    render() {
        return (
            <Container fluid={true} className={[style.contacts].join(' ')} id={'contacts'}>
                <Col sm="12" md={{ size: 8, offset: 2 }}>
                    <h1>CONTATOS</h1>
                    <img src={googleMaps} alt={' cenas'}></img>

                    {this.renderStoresInfo(storesInfo)}
                </Col>
            </Container>
        );
    }
}