import React, { Component } from 'react';

import footerLinks from '../footer/footer';
import { FooterLinksHttpEntity } from '../../../entities/Entities';

import style from './style.module.scss'

interface BudgetButtonProps {
}

interface BudgetButtonState {
}

let budgetObject: FooterLinksHttpEntity;

export default class BudgetButton extends Component<BudgetButtonProps, BudgetButtonState> {

    constructor(props: BudgetButtonProps) {
        super(props);

        this.renderBudget();
    }

    renderBudget() {

        footerLinks.forEach(element => {

            if (element.title === 'Orçamentos') {
                budgetObject = element;
            }
        });
    }

    render() {
        return (
            <div className={[style.budget_button].join(' ')}>
                <a href={budgetObject.url} className={[style.btn].join(' ')}>
                    <span>{budgetObject.title.toUpperCase()}</span>
                </a>
            </div>
        );
    }
}