import FooterLinks from './footer';
import React, { Component } from 'react';
import { Nav, NavLink, Navbar } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { FooterLinksHttpEntity } from '../../../entities/Entities';

import style from './style.module.scss';

interface FooterProps {

}

interface FooterState {

}

export default class Footer extends Component<FooterProps, FooterState> {

    renderFooter(footer: FooterLinksHttpEntity[]) {

        return (
            <Nav>
                {
                    footer
                        .map((footerLinks: FooterLinksHttpEntity, index: number) => {

                            return (
                                <NavLink key={index} href={footerLinks.url}>{footerLinks.title}</NavLink>
                            );
                        })
                }
            </Nav>
        );
    }

    render() {
        return (
            <div className={'footer'}>
                <Navbar
                // fixed="bottom" TODO: remove this so it's fixed
                className={[style.footer].join(' ')} expand="md">

                    {this.renderFooter(FooterLinks)}

                    <Nav className={[style.footer_copyright].join(' ')}>
                        <span>
                            Mega Loja Borja Reis © {(new Date().getFullYear())}
                        </span>
                        <a href={'/'}>
                            <FontAwesomeIcon size={'2x'} icon={'rss-square'} />
                        </a>
                        <a href={'/'}>
                            <FontAwesomeIcon size={'2x'} icon={'check-square'} />
                        </a>
                    </Nav>
                </Navbar>

            </div>
        );
    }
}