import React, { Component } from 'react';
import { Footer, TopBar, BudgetButton } from '../Fixed';
import { AboutSection, BrandSection, ContactSection } from '../Sections';

import style from './index.module.scss';
import ScrollToTopButton from '../Fixed/scroll-to-top-button';

interface IndexLayoutProps {
}

interface IndexLayoutState {
}

export default class IndexLayout extends Component<IndexLayoutProps, IndexLayoutState> {

    render() {
        return (
            <div className={[style.fullsize].join(' ')}>
                <TopBar />
                <BudgetButton />
                <AboutSection />
                <BrandSection />
                <ContactSection />
                <ScrollToTopButton />
                <Footer />
            </div>
        );
    }
}