import React, { Component } from 'react';
import { PDFReader } from 'reactjs-pdf-reader';

import ConstructionFlyer from '../../../../resources/flyers/construction_flyer.pdf';

export default class ReactPDF extends Component {

    render() {

        return (
            <div style={{ overflow: 'scroll', height: 750 }}>
                <PDFReader
                url={ConstructionFlyer}
                scale={2}
                showAllPage={true}
                />
            </div>
        );
    }
}